import { __awaiter } from "tslib";
import socket from '@/socket';
import { Self } from '@/vue-core';
import { action } from '@/alas';
import { defineComponent, reactive, watch, ref } from '@vue/composition-api';
export default defineComponent({
    props: {},
    setup(props, context) {
        let self = new Self(context);
        // =================
        //
        // ref
        //
        let done = ref(null);
        // =================
        //
        // state
        //
        let $ = reactive({
            done: false,
            dialog: false,
            result: []
        });
        // =================
        //
        // watch
        //
        watch(() => $.dialog, () => {
            $.done = false;
            $.result = [];
        });
        // =================
        //
        // methods
        //
        let close = () => {
            setTimeout(() => {
                $.dialog = false;
            }, 100);
        };
        let open = (project) => __awaiter(this, void 0, void 0, function* () {
            $.dialog = true;
            try {
                let packages = project.dependencies.items.map(s => s.name + '@' + s.version);
                let script = `npm install -g protractor & webdriver-manager update & npm install request-promise ${packages.join(' ')} --no-save`;
                socket.exec(script, (type, data) => {
                    if (type === 'exit') {
                        self.emit('done');
                        $.done = true;
                    }
                    else {
                        $.result.push(data);
                        setTimeout(() => done.value.parentNode.scrollTo(0, 99999999), 100);
                    }
                });
                self.nextTick(() => $.result.push(script));
            }
            catch (error) {
                action.message('error', error);
            }
        });
        // =================
        //
        // done
        //
        return {
            $,
            open,
            done,
            close
        };
    }
});
