import { status } from '@/alas';
import { defineComponent, reactive } from '@vue/composition-api';
export default defineComponent({
    props: {},
    setup() {
        // =================
        //
        // state
        //
        let $ = reactive({
            project: status.fetch('project'),
            dialog: false,
            callback: null,
            selected: []
        });
        // =================
        //
        // methods
        //
        let open = (callback) => {
            $.selected = [];
            $.callback = callback;
            $.dialog = true;
        };
        let selectAll = () => {
            if ($.selected.length === $.project.specs.size) {
                $.selected = [];
            }
            else {
                $.project.specs.forEach(model => {
                    if ($.selected.includes(model) === false) {
                        $.selected.push(model);
                    }
                });
            }
        };
        let confirm = () => {
            $.dialog = false;
            $.callback($.selected);
        };
        // =================
        //
        // done
        //
        return {
            $,
            open,
            selectAll,
            confirm
        };
    }
});
