import config from '../../config.json';
export function getConfig() {
    return config;
}
export function readFileText(file) {
    return new Promise(resolve => {
        let reader = new FileReader();
        reader.onload = event => resolve(event.target.result);
        reader.readAsText(file);
    });
}
export function copy(text) {
    let textarea = document.createElement('textarea');
    document.body.appendChild(textarea);
    textarea.innerText = text;
    textarea.select();
    textarea.focus();
    document.execCommand('copy');
    textarea.remove();
}
export function swapArray(list, index1, index2) {
    list.items[index1] = list.items.splice(index2, 1, list.items[index1])[0];
}
export function indexUp(list, index) {
    if (index + 1 != list.size) {
        swapArray(list, index, index + 1);
    }
}
export function indexDown(list, index) {
    if (index !== 0) {
        swapArray(list, index, index - 1);
    }
}
