import { Self } from '@/vue-core';
import { status } from '@/alas';
import { defineComponent, reactive } from '@vue/composition-api';
export default defineComponent({
    props: {},
    setup(props, context) {
        let self = new Self(context);
        // =================
        //
        // state
        //
        let $ = reactive({
            total: 5,
            message: status.fetch('message')
        });
        // =================
        //
        // methods
        //
        let click = (toast) => {
            toast.clicked = true;
        };
        let remove = (index) => {
            $.message.toasts[index].remove = true;
            $.message.$m.collecationToast();
        };
        let update = () => {
            let clear = false;
            let length = $.message.toasts.length;
            for (let i = 0; i < length; i++) {
                let toast = $.message.toasts[i];
                if (toast.clicked)
                    continue;
                if (toast.time < 0) {
                    toast.remove = true;
                    clear = true;
                }
                if (i < $.total) {
                    toast.time -= 1;
                }
            }
            if (clear) {
                $.message.$m.collecationToast();
            }
        };
        // =================
        //
        // timer
        //
        self.timer('count', 1000, () => {
            update();
        });
        // =================
        //
        // done
        //
        return {
            $,
            click,
            remove
        };
    }
});
