export const Options = {
    body: {
        open: [],
        title: []
    },
    defs: {
        open: () => false
    },
    methods: {
        open(self, title, callback) {
            self.open = true;
            self.title = title;
            self.$self.confirm = () => callback(() => {
                self.open = false;
                self.title = '';
            });
        }
    }
};
