import { Self } from '@/vue-core';
import { defineComponent, reactive, ref } from '@vue/composition-api';
export default defineComponent({
    props: {
        title: String,
        confirmName: String
    },
    setup(props, context) {
        let self = new Self(context);
        // =================
        //
        // refs
        //
        let form = ref(null);
        // =================
        //
        // state
        //
        let $ = reactive({
            dialog: false,
            validate: false,
            callback: null
        });
        // =================
        //
        // methods
        //
        let open = (callback) => {
            $.dialog = true;
            $.callback = callback;
            self.nextTick(() => form.value.reset());
        };
        let confirm = () => {
            $.dialog = false;
            if ($.callback) {
                $.callback();
                $.callback = null;
            }
        };
        // =================
        //
        // done
        //
        return {
            $,
            form,
            open,
            confirm
        };
    }
});
