export const Options = {
    body: {
        toasts: []
    },
    defs: {
        toasts: () => []
    },
    methods: {
        push(self, type, message) {
            self.toasts.push({
                time: 5,
                type: type,
                remove: false,
                clicked: false,
                message: message
            });
        },
        collecationToast(state) {
            state.toasts = state.toasts.filter(toast => toast.remove === false);
        }
    }
};
