import { status } from '@/alas';
import { defineComponent, reactive } from '@vue/composition-api';
export default defineComponent({
    props: {},
    setup() {
        // =================
        //
        // state
        //
        let $ = reactive({
            confirm: status.fetch('confirm')
        });
        // =================
        //
        // done
        //
        return {
            $
        };
    }
});
