import { v4 as uuidv4 } from 'uuid';
export const Options = {
    body: {
        id: [],
        key: [],
        value: [],
        createdAt: [],
        updatedAt: []
    },
    defs: {
        id: () => uuidv4(),
        key: () => '',
        value: () => '',
        createdAt: () => Date.now(),
        updatedAt: () => Date.now()
    },
    views: {
        tag() {
            return 'variable';
        },
        output(self) {
            if (self.$isChange()) {
                self.updatedAt = Date.now();
            }
            return {
                id: self.id,
                tag: self.$v.tag,
                key: self.key,
                value: self.value,
                createdAt: self.createdAt,
                updatedAt: self.updatedAt
            };
        }
    },
    list: {
        key: model => model.id,
        views: {
            output(list) {
                return list.items.map(s => s.$v.output);
            }
        }
    }
};
